<template>
  <div class="content container">
    <div class="loader__container" v-if="orderLoading">
     <Loader/>
    </div>
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name:'main'}">
            
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name:'Cart'}">
            
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
             
          </a>
        </li>
      </ul>

      <div class="content__row">
        <h1 class="content__title">
           
        </h1>
      </div>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST" @submit.prevent="order">
        <div class="cart__field">
          <div class="cart__data">
            <BaseFormText v-model="formData.name" title="" placeholder="   "
                          :error="formError.name"></BaseFormText>
            <BaseFormText v-model="formData.address" title=" "
                          placeholder="  " :error="formError.address"></BaseFormText>
            <BaseFormText v-model="formData.phone" title="" type="tel"
                          placeholder="  " :error="formError.phone"></BaseFormText>
            <BaseFormText v-model="formData.email" title="Email" type="email"
                          placeholder="  Email" :error="formError.email"></BaseFormText>
            <BaseFormTextArea title="  " :error="formError.comment"
                              v-model="formData.comment" placeholder=" "
                              name="comments"></BaseFormTextArea>
          </div>

          <div class="cart__options">
            <h3 class="cart__title"></h3>
            <ul class="cart__options options">
              <li class="options__item" v-for="(delivery, index) in deliveryTypeId" :key="index">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" v-model="formData.deliveryTypeId" :value="delivery.id" checked="">
                  <span class="options__value">
                    {{delivery.title}} <b>{{delivery.price | numberFormat}} </b>
                  </span>
                </label>
              </li>
            </ul>
            <h3 class="cart__title"></h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" v-model="formData.paymentTypeId" :value="1" checked="">
                  <span class="options__value">
                      
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" v-model="formData.paymentTypeId" :value="2">
                  <span class="options__value">
                      
                  </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <li class="cart__order" v-for="(product, index) in products" :key="index" :product="product">
              <h3>{{product.product.title}}</h3>
              <b>{{product.price | numberFormat}}</b>
              <span>: {{product.id}}</span>
            </li>
          </ul>
          <div class="cart__total">
            <p>: <b>{{deliveryClub}}</b></p>
            <p>: <b>{{totalProducts}}</b>    <b>{{total | numberFormat}}</b></p>
          </div>

          <button class="cart__button button button--primery" type="submit">
             
          </button>
        </div>
        <div class="cart__error form__error-block" v-if="formError">
          <h4>  !</h4>
          <p>
            {{formError.paymentTypeId}}
          </p>
        </div>
      </form>
    </section>
  </div>
</template>
<script>
import { mapGetters } from 'vuex'
import axios from 'axios'
import BaseFormTextArea from '../components/BaseFormTextArea'
import BaseFormText from '../components/BaseFormText'
import Loader from '../components/Loader'
import numberFormat from '../helpers/numberFormat'
export default {
  filters: {
    numberFormat
  },
  computed: {
    ...mapGetters({ products: 'cartDetailProducts', totalPrice: 'cartTotalPrice', totalProducts: 'cartTotalProducts' }),
    deliveryClub () {
      // eslint-disable-next-line no-unused-vars
      const choosenDelivery = this.deliveryTypeId.find(item => item.id === this.formData.deliveryTypeId)
      return choosenDelivery && choosenDelivery.title
    },
    deliveryClubPrice () {
      // eslint-disable-next-line no-unused-vars
      const choosenDeliveryPrice = this.deliveryTypeId.find(item => item.id === this.formData.deliveryTypeId)
      return choosenDeliveryPrice && choosenDeliveryPrice.price
    },
    total () {
      const totalPriceNum = this.totalPrice ? parseFloat(this.totalPrice) : 0
      const deliveryPriceNum = this.deliveryClubPrice ? parseFloat(this.deliveryClubPrice) : 0
      return totalPriceNum + deliveryPriceNum
    }
  },
  data () {
    return {
      formData: {},
      formError: '',
      formErrorDelivery: {},
      formErrorMessage: '',
      orderLoading: false,
      deliveryTypeId: [],
      paymentTypeId: 1

    }
  },
  methods: {
    order () {
      this.formError = {}
      this.formErrorMessage = ''
      this.orderLoading = true
      axios
        .post('https://vue-moire.skillbox.cc/api/orders', {
          ...this.formData
        }, {
          params: {
            userAccessKey: this.$store.state.userAccessKey
          }
        })
        .then(response => {
          console.log(response)
          this.orderLoading = false
          this.$store.commit('resetCart')
          this.$store.commit('updateOrderInfo', response.data)
          this.$router.push({
            name: 'info',
            params: { id: response.data.id }
          })
        })
        .catch(error => {
          this.orderLoading = false
          this.formError = error.response.data.error.request
          this.formErrorMessage = error.response.data.error.message
        })
    },
    delivery () {
      axios
        .get('https://vue-moire.skillbox.cc/api/deliveries')
        .then(response => {
          console.log(response)
          this.deliveryTypeId = response.data
        })
    }
  },
  created () {
    this.delivery()
  },
  components: {
    BaseFormText,
    BaseFormTextArea,
    Loader
  }
}
</script>
<style>
  .loader__container {
    margin: 0;
    height: 40vh;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
  }
</style>
