<template>
  <ul>
    <li class="catalog__item">
      <router-link class="catalog__pic" :to="{name:'product', params: {id: product.id}}">
        <img :src="previewImageLink" alt="product.title">
      </router-link>

      <h3 class="catalog__title from-bottom">
        <router-link :to="{name:'product', params: {id: product.id}}">
          {{product.title}}
        </router-link>
      </h3>

      <span class="catalog__price">
              {{product.price | numberFormat}} 
            </span>

      <ul class="colors colors--black">
        <li class="colors__item" :key="index" v-for="(color,index) in product.colors">
          <label class="colors__label">
            <input class="colors__radio sr-only" type="radio" v-model="selectedColor" name="color-1" :value="index"
                   checked="">
            <span class="colors__value" :style="{'background-color': color.color.code}">
                  </span>
          </label>
        </li>
      </ul>
    </li>
  </ul>
</template>
<script>
import numberFormat from '../helpers/numberFormat'
export default {
  props: ['product'],
  computed: {
    previewImageLink () {
      return this.product?.colors[this.selectedColor].gallery ? this.product?.colors[this.selectedColor].gallery[0]?.file?.url : 'https://tr.alfa-cds.com/wp-content/uploads/images/pochemu_u_koshki_ploho_pahnet_izo_rta_kps.jpg'
    }
  },
  data () {
    return {
      selectedColor: 0
    }
  },
  filters: {
    numberFormat
  }
}
</script>
<style lang="scss">
  ul {
    list-style: none;
  }
</style>
