<template>
  <div class="content container" v-if="productsData">
    <div class="loader__container" v-if="productLoading">
      <Loader/>
    </div>
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name:'main'}">
            
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            {{productsData.title}}
          </a>
        </li>
      </ul>
    </div>

    <section class="item">
      <div class="item__pics pics">
        <div class="pics__wrapper">
          <img width="470" height="470" :src="previewImageLink"
               alt=" ">
        </div>
        <ul class="pics__list">
          <li class="pics__item" v-for="(image, index) in colorsImage" :key="index" >
            <span href="" class="pics__link pics__link--current">
              <img width="98" height="98" :src="image.file.url" :srcset="image.file.url"
                   alt=" ">
            </span>
          </li>
        </ul>
      </div>

      <div class="item__info">
        <span class="item__code">: {{productsData.id}}</span>
        <h2 class="item__title">
          {{productsData.title}}
        </h2>
        <div class="item__form">
          <form class="form" action="#" method="POST" @submit.prevent="addToCart">
            <div class="item__row item__row--center">
              <div class="form__counter">
                <button type="button" aria-label="  " @click="productAmount = productAmount === 0 ? productAmount : productAmount - 1">
                  <svg width="12" height="12" fill="currentColor">
                    <use xlink:href="#icon-minus"></use>
                  </svg>
                </button>

                <input type="text" value="1" name="count" v-model.number="productAmount">

                <button type="button" aria-label="  " @click="productAmount += 1">
                  <svg width="12" height="12" fill="currentColor">
                    <use xlink:href="#icon-plus"></use>
                  </svg>
                </button>
              </div>

              <b class="item__price">
                {{productsData.price | numberFormat}}
              </b>
            </div>

            <div class="item__row">
              <fieldset class="form__block">
                <legend class="form__legend"></legend>
                <ul class="colors colors--black">
                  <li class="colors__item" :key="index" v-for="(color,index) in productsData.colors">
                    <label class="colors__label">
                      <input class="colors__radio sr-only" type="radio" v-model="selectedColor" name="color-1" :value="color.color.id"
                             checked="">
                      <span class="colors__value" :style="{'background-color': color.color.code}">
                  </span>
                    </label>
                  </li>
                </ul>
              </fieldset>

              <fieldset class="form__block">
                <legend class="form__legend"></legend>
                <label class="form__label form__label--small form__label--select">
                  <select class="form__select" type="text" name="category" v-model="selectedSize">
                    <option v-for="size in productsData.sizes" :key="size.id" :value="size.id">{{size.title}}</option>
                  </select>
                </label>
              </fieldset>
            </div>

            <button class="item__button button button--primery" type="submit">
               
            </button>
            <div v-show="productAdded" style="margin-top: 15px">   </div>
            <div v-show="productAddSending" style="margin-top: 15px">   </div>
          </form>
        </div>
      </div>

      <div class="item__desc">
        <ul class="tabs">
          <li class="tabs__item">
            <a class="tabs__link tabs__link--current">
                
            </a>
          </li>
          <li class="tabs__item">
            <a class="tabs__link" href="#">
                
            </a>
          </li>
        </ul>

        <div class="item__content">
          <h3>:</h3>

          <p>
            60% <br>
            30% <br>
          </p>

          <h3>:</h3>

          <p>
               . 30C  <br>
              . 110C<br>
               <br>
             <br>
              <br>
          </p>

        </div>
      </div>
    </section>
  </div>
</template>
<script>
import axios from 'axios'
import { mapActions } from 'vuex'
import numberFormat from '../helpers/numberFormat'
import Loader from '../components/Loader'
export default {
  components: {
    Loader
  },
  data () {
    return {
      productsData: null,
      selectedColor: 0,
      selectedSize: 'Size',
      productAmount: 1,
      productAdded: false,
      productAddSending: false,
      productLoading: false
    }
  },
  filters: {
    numberFormat
  },
  computed: {
    colorsImage () {
      const choosenColor = this.productsData.colors.find(color => color.color.id === this.selectedColor)
      return choosenColor && choosenColor.gallery ? choosenColor.gallery : [{ file: { url: 'https://tr.alfa-cds.com/wp-content/uploads/images/pochemu_u_koshki_ploho_pahnet_izo_rta_kps.jpg' } }]
    },
    previewImageLink () {
      const choosenColor = this.productsData.colors.find(color => color.color.id === this.selectedColor)
      return choosenColor && choosenColor.gallery ? choosenColor.gallery[0]?.file?.url : 'https://tr.alfa-cds.com/wp-content/uploads/images/pochemu_u_koshki_ploho_pahnet_izo_rta_kps.jpg'
    }
  },
  methods: {
    loadProducts () {
      this.productLoading = true
      return axios
        .get('https://vue-moire.skillbox.cc/api/products/' + this.$route.params.id)
        .then(response => {
          this.productsData = response.data
          this.selectedSize = response.data.sizes[0].id
          this.selectedColor = response.data.colors[0].color.id
          this.productLoading = false
        })
    },
    ...mapActions(['addProductToCart']),
    addToCart () {
      this.productAdded = false
      this.productAddSending = true
      this.addProductToCart({
        productId: String(this.productsData.id),
        quantity: String(this.productAmount),
        colorId: String(this.selectedColor),
        sizeId: String(this.selectedSize)
      })
        .then(() => {
          this.productAdded = true
          this.productAddSending = false
        })
    }
  },
  created () {
    this.loadProducts()
  },
  watch: {
    '$route.params.id' () {
      this.loadProducts()
    }
  }
}
</script>
<style lang="scss">
  .loader__container {
    margin: 0;
    height: 40vh;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
  }
</style>
