<template>
  <div class="content container">
    <div class="content__top">

      <div class="content__row">
        <h1 class="content__title">
          
        </h1>
        <span class="content__info">
          {{countProducts}} 
        </span>
      </div>
    </div>

    <div class="content__catalog">
      <ProductFilter :price-from.sync="filterPriceFrom"
                     :price-to.sync="filterPriceTo"
                     :category-id.sync="filterCategoryId"
                     :color.sync="filterColor"
                     :material.sync="filterMaterials"
                     :collection.sync="filterSeason"></ProductFilter>

      <section class="catalog">
        <div class="loader__container" v-if="productsLoading">
          <Loader/>
        </div>
        <ProductList v-bind:products="products"></ProductList>
        <a href="tel:+7 (800) 600 90 09" id="popup__toggle">
          <div class="circlephone" style="transform-origin: center;"></div>
          <div class="circle-fill" style="transform-origin: center;"></div>
          <div class="img-circle" style="transform-origin: center;">
            <div class="img-circleblock" style="transform-origin: center;"></div>
          </div>
        </a>
        <Pagination v-model="page" :count="countProducts" :perPage="productsPerPage"></Pagination>
      </section>
    </div>
  </div>
</template>
<script>
import ProductList from '../components/ProductList'
import Pagination from '../components/Pagination'
import ProductFilter from '../components/ProductFilter'
import Loader from '../components/Loader'
import axios from 'axios'

export default {
  components: {
    ProductList,
    Pagination,
    ProductFilter,
    Loader
  },
  data () {
    return {
      filterPriceTo: 0,
      filterPriceFrom: 0,
      filterCategoryId: 0,
      filterColor: '',
      filterMaterials: [],
      filterSeason: [],
      page: 1,
      productsPerPage: 3,
      productsData: null,
      productsLoading: false

    }
  },
  computed: {
    products () {
      return this.productsData ? this.productsData.items : []
    },
    countProducts () {
      return this.productsData && this.productsData.pagination ? this.productsData.pagination.total : 0
    }
  },
  methods: {
    loadProducts () {
      this.productsLoading = true
      axios
        .get('https://vue-moire.skillbox.cc/api/products', {
          params: {
            page: this.page,
            categoryId: this.filterCategoryId,
            minPrice: this.filterPriceFrom,
            maxPrice: this.filterPriceTo,
            limit: this.productsPerPage,
            colorIds: this.filterColor,
            materialIds: this.filterMaterials,
            seasonIds: this.filterSeason

          }
        })
        .then(response => {
          this.productsData = response.data
          this.productsLoading = false
        })
    }
  },
  created () {
    this.loadProducts()
  },
  watch: {
    page () {
      this.loadProducts()
    },
    filterPriceFrom () {
      this.loadProducts()
    },
    filterPriceTo () {
      this.loadProducts()
    },
    filterCategoryId () {
      this.page = 1
      this.loadProducts()
    },
    filterColor () {
      this.loadProducts()
    },
    filterMaterials () {
      this.page = 1
      this.loadProducts()
    },
    filterSeason () {
      this.page = 1
      this.loadProducts()
    }
  }
}
</script>
