<template>
  <div>
    <main class="content container">
      <div class="content__top">
        <ul class="breadcrumbs">
          <li class="breadcrumbs__item">
            <router-link class="breadcrumbs__link" :to="{name:'main'}">
              
            </router-link>
          </li>
        </ul>

        <div class="content__row">
          <h1 class="content__title">
            
          </h1>
          <span class="content__info">
          {{totalProducts}}
        </span>
        </div>
      </div>

      <section class="cart">
        <form class="cart__form form" action="#" method="POST">
          <div class="cart__field">
            <ul class="cart__list" v-for="(item, index) in products" :key="index" :item="item" >
              <li class="cart__item product">
                <div class="product__pic">
                  <img :src="CartImage(item.color)" width="120" height="120" alt=" ">
                </div>
                <h3 class="product__title">
                  {{item.product.title}}
                </h3>
                <p class="product__info product__info--color">
                  :
                  <span>
                  <i :style="{'background-color': cartColor(item.color)}"></i>
                  {{cartColorTitle(item.color)}}
                </span>
                </p>
                <span class="product__code">
                : {{item.id}}
              </span>

                <div class="product__counter form__counter">
                  <button type="button" aria-label="  "
                          @click="item.quantity = item.quantity === 0 ? item.quantity : item.quantity - 1">
                    <svg width="10" height="10" fill="currentColor">
                      <use xlink:href="#icon-minus"></use>
                    </svg>
                  </button>

                  <input type="text" v-model.number="item.quantity" name="count">

                  <button type="button" aria-label="  " @click="item.quantity += 1">
                    <svg width="10" height="10" fill="currentColor">
                      <use xlink:href="#icon-plus"></use>
                    </svg>
                  </button>
                </div>

                <b class="product__price">
                  {{item.product.price}}
                </b>

                <button class="product__del button-del" type="button" @click.prevent="deleteProduct(item.id)"
                        aria-label="   ">
                  <svg width="20" height="20" fill="currentColor">
                    <use xlink:href="#icon-close"></use>
                  </svg>
                </button>
              </li>
            </ul>
          </div>

          <div class="cart__block">
            <p class="cart__desc">
              &nbsp;   &nbsp; 
            </p>
            <p class="cart__price">
              : <span>{{totalPrice | numberFormat}}</span>
            </p>

            <router-link tag="button" type="submit" class="cart__button button button--primery" :to="{name:'order'}">
               
            </router-link>
          </div>
        </form>
      </section>
    </main>
  </div>
</template>
<script>
import { mapActions, mapGetters } from 'vuex'
import numberFormat from '../helpers/numberFormat'

export default {
  filters:
      { numberFormat },
  computed: {
    ...mapGetters({
      products: 'cartDetailProducts',
      totalPrice: 'cartTotalPrice',
      totalProducts: 'cartTotalProducts'
    })
  },
  methods: {
    ...mapActions({ deleteProduct: 'deleteCartProduct' }),
    CartImage (color) {
      return color?.gallery ? color?.gallery[0]?.file?.url : 'https://tr.alfa-cds.com/wp-content/uploads/images/pochemu_u_koshki_ploho_pahnet_izo_rta_kps.jpg'
    },
    cartColor (color) {
      return color?.color ? color.color.code : null
    },
    cartColorTitle (color) {
      return color?.color ? color.color.title : null
    }
  }
}
</script>
