<template>
  <div class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name:'main'}">
            
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name:'Cart'}">
            
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
             
          </a>
        </li>
      </ul>

      <h1 class="content__title">
          <span> {{showOrderInfo.id}}</span>
      </h1>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <p class="cart__message">
             &nbsp;  . &nbsp;    &nbsp; .
               &nbsp; &nbsp;     .
          </p>

          <ul class="dictionary">
            <li class="dictionary__item">
              <span class="dictionary__key">
                
              </span>
              <span class="dictionary__value">
                {{showOrderInfo.name}}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                 
              </span>
              <span class="dictionary__value">
                {{showOrderInfo.address}}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                
              </span>
              <span class="dictionary__value">
                {{showOrderInfo.phone}}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Email
              </span>
              <span class="dictionary__value">
                {{showOrderInfo.email}}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                 
              </span>
              <span class="dictionary__value">
                {{showOrderInfo.paymentType}}
              </span>
            </li>
          </ul>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <li class="cart__order" v-for="product in showOrderInfo.basket.items" :key="product.id">
              <h3>{{product.product.title}}</h3>
              <b>{{product.product.price | numberFormat}}</b>
              <span>: {{product.product.id}}</span>
            </li>
          </ul>

          <div class="cart__total">
            <p>: <b>{{showOrderInfo.deliveryType.title}} {{showOrderInfo.deliveryType.price | numberFormat}} </b></p>
            <p>: <b>{{showOrderInfo.basket.items.length}}</b>    <b>{{showOrderInfo.totalPrice | numberFormat}} </b></p>
          </div>
        </div>
      </form>
    </section>
  </div>
</template>
<script>
import { mapGetters } from 'vuex'
import numberFormat from '../helpers/numberFormat'
export default {
  filters: {
    numberFormat
  },
  computed: {
    ...mapGetters({ showOrderInfo: 'reloadOrderInfo' })
  },
  created () {
    this.$store.dispatch('loadOrderInfo', this.$route.params.id)
  }
}
</script>
