<template>
  <aside class="filter">
    <form class="filter__form form" action="#" method="get" @submit.prevent="submit">
      <fieldset class="form__block">
        <legend class="form__legend"></legend>
        <label class="form__label form__label--price">
          <input class="form__input" type="text" name="min-price" value="0" v-model.number="currentPriceFrom">
          <span class="form__value"></span>
        </label>
        <label class="form__label form__label--price">
          <input class="form__input" type="text" name="max-price" value="0" v-model.number="currentPriceTo">
          <span class="form__value"></span>
        </label>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend"></legend>
        <label class="form__label form__label--select">
          <select class="form__select" type="text" name="category" v-model.number="currentCategoryId">
            <option value="0"> </option>
            <option :value="category.id"
                    v-for="category in categories"
                    :key="category.id">{{category.title}}
            </option>

          </select>
        </label>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend"></legend>
        <ul class="check-list">
          <li class="check-list__item" v-for="items in materials" :key="items.id">
            <label class="check-list__label">
              <input class="check-list__check sr-only" v-model="Selected"  type="checkbox" name="material" :value="items.id">
              <span class="check-list__desc">
                    {{items.title}}
                    <span>{{items.productsCount}}</span>
                  </span>
            </label>
          </li>
        </ul>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend"></legend>
        <ul class="check-list">
          <li class="check-list__item" :key="index" v-for="(items, index) in collectionData">
            <label class="check-list__label">
              <input class="check-list__check sr-only" v-model="collectionSelectedData[index]" type="checkbox"
                     name="collection" value="">
              <span class="check-list__desc">
                    {{items.title}}
                    <span>({{items.productsCount}})</span>
                  </span>
            </label>
          </li>
        </ul>
      </fieldset>

      <button class="filter__submit button button--primery" type="submit" @click.prevent="submit">
        
      </button>
      <button class="filter__reset button button--second" type="button" @click.prevent="reset">
        
      </button>
    </form>
  </aside>

</template>
<script>
import axios from 'axios'

export default {
  props: ['priceFrom', 'priceTo', 'categoryId', 'color', 'material', 'collection'],
  data () {
    return {
      Selected: [],
      currentPriceFrom: 0,
      currentPriceTo: 0,
      currentCategoryId: 0,
      currentColor: '#FFFFFF',
      categoriesData: null,
      colorsData: null,
      collectionData: [],
      collectionSelectedData: [],
      materialData: []
    }
  },
  computed: {
    categories () {
      return this.categoriesData ? this.categoriesData.items : []
    },
    colors () {
      return this.colorsData ? this.colorsData.items : []
    },
    materials () {
      return this.materialData ? this.materialData.items : []
    },
    collections () {
      return this.collectionData ? this.collectionData.items : []
    }
  },
  watch: {
    priceFrom (value) {
      this.currentPriceFrom = value
    },
    priceTo (value) {
      this.currentPriceTo = value
    },
    categoryId (value) {
      this.currentCategoryId = value
    },
    color (value) {
      this.currentColor = value
    },
    material (value) {
      this.Selected = value
    }
  },
  methods: {
    submit () {
      this.$emit('update:priceFrom', this.currentPriceFrom)
      this.$emit('update:priceTo', this.currentPriceTo)
      this.$emit('update:categoryId', this.currentCategoryId)
      this.$emit('update:color', this.currentColor)
      const collectionArray = []
      for (let i = 0; i < this.collectionSelectedData.length; i++) {
        if (this.collectionSelectedData[i]) {
          collectionArray.push(this.collectionData[i].id)
        }
      }
      this.$emit('update:collection', collectionArray)
      this.$emit('update:material', this.Selected)
    },
    reset () {
      this.$emit('update:priceFrom', 0)
      this.$emit('update:priceTo', 0)
      this.$emit('update:categoryId', 0)
      this.$emit('update:color', '#FFFFFF')
      this.$emit('update:collection', null)
      this.$emit('update:material', null)
    },
    loadCategories () {
      console.log('Load categories')
      axios
        .get('https://vue-moire.skillbox.cc/api/productCategories')
        .then(response => {
          console.log(response)
          this.categoriesData = response.data
        })
        .catch(error => {
          console.log('Error is>>>>>>>>>>>>>>', error)
        })
    },
    loadColors () {
      axios
        .get('https://vue-moire.skillbox.cc/api/colors', {
          'Content-type': 'application/json'
        })
        .then(response => {
          this.colorsData = response.data
        })
        .catch(error => {
          console.log('Error is>>>>>>>>>>>>>>', error)
        })
    },
    loadMaterials () {
      axios
        .get('https://vue-moire.skillbox.cc/api/materials', {
          'Content-type': 'application/json'
        })
        .then(response => {
          this.materialData = response.data
        })
        .catch(error => {
          console.log('Error is>>>>>>>>>>>>>>', error)
        })
    },
    loadCollection () {
      axios
        .get('https://vue-moire.skillbox.cc/api/seasons', {
          'Content-type': 'application/json'
        })
        .then(response => {
          this.collectionData = response.data.items
        })
        .catch(error => {
          console.log('Error is>>>>>>>>>>>>>>', error)
        })
    }
  },
  created () {
    this.loadCategories()
    this.loadColors()
    this.loadCollection()
    this.loadMaterials()
  }
}
</script>
