import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    cartProducts: [],
    userAccessKey: null,
    cartProductsData: [],
    orderInfo: null
  },
  mutations: {
    updateOrderInfo (state, orderInfo) {
      state.orderInfo = orderInfo
    },
    resetCart (state) {
      state.cartProducts = []
      state.cartProductsData = []
    },
    updateCartProductAmount (state, { productId, quantity }) {
      const item = state.cartProducts.find(item => item.productId === productId)
      if (item) {
        item.quantity = quantity
        item.productId = productId
      }
    },
    deleteCartProduct (state, productId) {
      state.cartProducts = state.cartProducts.filter(item => item.productId !== productId)
    },
    updateUserAccessKey (state, accessKey) {
      state.userAccessKey = accessKey
    },
    updateCartProductsData (state, items) {
      state.cartProductsData = items
    },
    syncCartProducts (state) {
      state.cartProducts = state.cartProductsData.map(item => {
        return {
          productId: item.product.id,
          quantity: item.quantity
        }
      })
    }
  },
  getters: {
    cartDetailProducts (state) {
      return state.cartProductsData
    },
    cartTotalPrice (state, getters) {
      // eslint-disable-next-line max-len
      return getters.cartDetailProducts.reduce((acc, item) => (item.price * item.quantity) + acc, 0)
    },
    cartTotalProducts (state, getters) {
      // eslint-disable-next-line max-len
      return getters.cartDetailProducts.reduce((acc, item) => (acc + item.quantity), 0)
    },
    reloadOrderInfo (state) {
      return state.orderInfo
    }
  },
  actions: {
    loadOrderInfo (context, orderId) {
      return axios
        .get('https://vue-moire.skillbox.cc/api/orders/' + orderId, {
          params: {
            userAccessKey: context.state.userAccessKey
          }
        })
        .then(response => {
          context.commit('updateOrderInfo', response.data)
        })
    },
    loadCart (context) {
      return axios
        .get('https://vue-moire.skillbox.cc/api/baskets', {
          params: {
            userAccessKey: context.state.userAccessKey
          }

        })
        .then(response => {
          if (!context.state.userAccessKey) {
            sessionStorage.setItem('userAccessKey', response.data.user.accessKey)
            context.commit('updateUserAccessKey', response.data.user.accessKey)
          }
          // eslint-disable-next-line no-unused-expressions
          context.commit('updateCartProductsData', response.data.items)
          context.commit('syncCartProducts')
        })
    },
    addProductToCart (context, { productId, quantity, colorId, sizeId }) {
      return axios
        .post('https://vue-moire.skillbox.cc/api/baskets/products', {
          productId: productId,
          quantity: quantity,
          colorId: colorId,
          sizeId: sizeId
        }, {
          params: {
            userAccessKey: context.state.userAccessKey
          }
        }
        )
        .then(response => {
          context.commit('updateCartProductsData', response.data.items)
          context.commit('syncCartProducts')
        })
    },
    updateCartProductAmount (context, { id, quantity }) {
      context.commit('updateCartProductAmount', {
        id,
        quantity
      })
      if (quantity < 1) {
        return
      }
      return axios
        .put('https://vue-moire.skillbox.cc/api/baskets/products', {
          basketItemId: id,
          quantity: quantity
        }, {
          params: {
            userAccessKey: context.state.userAccessKey
          }
        }
        )
        .then(response => {
          context.commit('updateCartProductsData', response.data.items)
        })
        .catch(() => {
          context.commit('syncCartProducts')
        })
    },
    deleteCartProduct (context, id) {
      console.log('delete')
      context.state.cartProducts = context.state.cartProducts.filter(item => item.id !== id)
      return axios
        .delete('https://vue-moire.skillbox.cc/api/baskets/products', {
          data: {
            basketItemId: id
          },
          params: {
            userAccessKey: context.state.userAccessKey
          }

        }
        )
        .then(response => {
          context.commit('updateCartProductsData', response.data.items)
        })
        .catch(() => {
          context.commit('syncCartProducts')
        })
    }
  },
  modules: {}
})
